
html,body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}
body {
  display: -ms-flexbox;
  display: flex;
  background-image: url(./assets/bg2.jpg);
  background-size: cover;
}
.loginBox {
  width: 400px;
  padding: 50px;
  background: url(./assets/bg2.jpg) center;
  border-radius: 10px;
  margin: auto;
}
.loginBox > div {
  margin: 10px 0;
}
.text-right {
  text-align: right;
}



